import React, { Component } from 'react';
import axios from 'axios';
import Pokemon from "./Pokemon";
import {getLocation} from "./functions.js";
import './PokemonList.css';
import Tooltip from "material-ui/Tooltip";

class PokemonList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            secondsElapsed: 0,
            location: null,
            pokemon: []
        };
        this.tick = this.tick.bind(this);
    }

    render() {

        const pokemon = this.state.pokemon.map((pokemon, i) => {
            return (
                <Tooltip id="tooltip-bottom" title="Click to show in map" key={i}>
                    <div className="listItem">
                            <Pokemon pokemon={pokemon} location={this.state.location}/>
                    </div>
                </Tooltip>
            )
        });

        return (
            <div className="container">
                {pokemon}
            </div>
        );
    }

    componentDidMount() {

        getLocation((location) => {
            this.setState({location});
        });

        axios.get('https://folk.ntnu.no/martsti/Pokefy/pokemon.php?type=all')
            .then( (response) => {
                this.setState({pokemon : response.data.pokemon});
            })
            .catch((error) => {
                console.log(error);
            });

        this.interval = setInterval(this.tick, 1000);
    }

    tick() {
        if (this.state.secondsElapsed === 59) {
            this.setState({secondsElapsed : 0});
            axios.get('https://folk.ntnu.no/martsti/Pokefy/pokemon.php?type=all')
                .then( (response) => {
                    this.setState({pokemon : response.data.pokemon});
                })
                .catch((error) => {
                    console.log(error);
                });
        }
        else {
            this.setState({secondsElapsed: this.state.secondsElapsed + 1});
        }
        if (this.state.secondsElapsed % 10 === 0) {
            getLocation((location) => {
                this.setState({location});
            });
        }
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }
}

export default PokemonList;


