import React, { Component } from 'react';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';
import './Pokemon.css';
import {getDistance, color} from "./functions";


class Pokemon extends Component {

    render() {

        let distance = null;
        if (this.props.location != null) {
            distance = (
                <Typography component="p">
                    Distance: {parseInt(getDistance(this.props.pokemon.lat, this.props.pokemon.lon, this.props.location.coords.latitude, this.props.location.coords.longitude), 10)} meters
                </Typography>
            )
        }

        let timeLeft = null;

        const currentDate = new Date();
        if (this.props.pokemon.time > currentDate.getTime()) {

            // Calculate the difference in milliseconds
            let difference_ms = this.props.pokemon.time - currentDate.getTime();
            //take out milliseconds
            difference_ms = difference_ms/1000;
            let seconds = Math.floor(difference_ms % 60);
            difference_ms = difference_ms/60;
            let minutes = Math.floor(difference_ms % 60);

            if (minutes < 10) {minutes = "0"+minutes;}
            if (seconds < 10) {seconds = "0"+seconds;}

            timeLeft = (<span className="active">{minutes + ":" + seconds}</span>);
        }
        else {
            timeLeft = (<span className="expired">EXPIRED</span>);
        }


        let ivColor = null;
        if (parseInt(this.props.pokemon.iv, 10) === 100) {
            // ivColor = "#FFD700";
        }

        return (
            <div onClick={this.showLocation.bind(this)}>
                <Paper className="container" elevation={1} style={{backgroundColor: ivColor}}>
                    <div className="pokemonImageContainer">
                        <img src={"pokemon/" + this.props.pokemon.name + ".png"} alt="Pokémon"/>
                    </div>
                    <div className="container2">
                        <div className="container3">
                            <Typography variant="headline" component="h3">
                                {this.props.pokemon.name}
                            </Typography>
                            <Typography variant="headline" component="h3" style={{color: color(this.props.pokemon.iv)}}>
                                {this.props.pokemon.iv}%
                            </Typography>
                        </div>
                        <div className="iconContainer">
                            <img src="icons/sword.png" alt="Attack stat"/>
                            <span><Typography>{this.props.pokemon.atk}</Typography></span>
                            <img src="icons/shield.png" alt="Defence stat"/>
                            <span><Typography>{this.props.pokemon.def}</Typography></span>
                            <img src="icons/heart.png" alt="Stamina stat"/>
                            <span><Typography>{this.props.pokemon.sta}</Typography></span>
                        </div>
                        <Typography component="p">
                            Level: {this.props.pokemon.level}
                        </Typography>
                        {distance}
                        <Typography component="p">
                            Time left: {timeLeft}
                        </Typography>

                    </div>
                </Paper>
            </div>
        );
    }

    showLocation() {
        window.open("http://www.google.com/maps?q=" + this.props.pokemon.lat + ", " + this.props.pokemon.lon, '_blank');
    }

}

export default Pokemon;


