import React from 'react';
import Drawer from 'material-ui/Drawer';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';
import SettingsIcon from 'material-ui-icons/Settings';
import AboutIcon from 'material-ui-icons/LightbulbOutline';
// import MoneyIcon from 'material-ui-icons/AttachMoney';

const styles = {
    list: {
        width: 250,
    }
};

export default class PokefyDrawer extends React.Component {

    render() {
        const sideList = (
            <div style={styles.list}>
                <List component="nav">
                    <ListItem button>
                        <ListItemIcon>
                            <SettingsIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Settings" />
                    </ListItem>
                    <ListItem button>
                        <ListItemIcon>
                            <AboutIcon />
                        </ListItemIcon>
                        <ListItemText primary="About" />
                    </ListItem>
                    {/*<ListItem button>*/}
                        {/*<ListItemIcon>*/}
                            {/*<MoneyIcon />*/}
                        {/*</ListItemIcon>*/}
                        {/*<ListItemText primary="Donate" />*/}
                    {/*</ListItem>*/}
                </List>
            </div>
        );

        return (
            <div>
                <Drawer open={this.props.open} onClose={this.props.onToggleDrawer}>
                    <div
                        tabIndex={0}
                        role="button"
                    >
                        {sideList}
                    </div>
                </Drawer>
            </div>
        );
    }
}


