import React from 'react';
import AppBar from 'material-ui/AppBar';
import PokefyDrawer from "./PokefyDrawer";
import Toolbar from "material-ui/Toolbar";
import IconButton from "material-ui/IconButton";
import Typography from "material-ui/Typography";
import MenuIcon from 'material-ui-icons/Menu';

const styles = {
    root: {
        flexGrow: 1,
    },
    flex: {
        flex: 1,
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
};

export default class AppBarTop extends React.Component {

    constructor() {
        super();
        this.state = {
            open: false
        }
    }

    toggleDrawer() {
        this.setState({
            open: !this.state.open
        })
    }

    render() {
        return (
            <div>
                <div style={styles.root}>
                    <AppBar
                        position="static"
                        title="Pokefy"
                    >
                        <Toolbar>
                            <IconButton style={styles.menuButton} color="inherit" aria-label="Menu" onClick={this.toggleDrawer.bind(this)}>
                                <MenuIcon />
                            </IconButton>
                            <Typography variant="title" color="inherit" style={styles.flex}>
                                Pokefy
                            </Typography>
                            {/*<Button color="inherit">Login</Button>*/}
                        </Toolbar>
                    </AppBar>
                </div>
                <PokefyDrawer open={this.state.open} onToggleDrawer={this.toggleDrawer.bind(this)} />
            </div>
        )
    }
}


